@import 'variables';

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  .width-wrapper {
    padding: 20px;
    max-width: 1600px;
    margin: 0 auto;
    width: 100%;
    @media screen and (min-width: 540px) {
      padding: 4em;
    }
  }
  .text-wrapper {
    max-width: 850px;
    margin: 0 auto;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.input-file {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

// Fix Semantic-ui inline fields
.ui.form .inline.fields > .field:last-child {
  padding: 0;
}

.dimmer .content {
  width: 100%;
}

.port {
  background: $lightgray;
  padding: 10px;
  border-radius: 3px;
  border: 1px solid darken($lightgray, 10%);
  &--open {
    background: rgba($lightgreen, 0.15);
    border-color: $lightgreen;
  }
  &--closed {
    background: rgba($negative, 0.15);
    border-color: $negative;
  }
}

.header-section {
  color: $offwhite;
  padding: 20px 0;
  position: relative;
  .ui.header {
    color: $offwhite;
  }
  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 100vw;
    height: 500px;
    min-width: 400px;
    transform: translate(-50%, 0);
    background: $darkbkg;
    z-index: -1;
  }
}

.ui.header.light-header {
  color: $offwhite;
}

@import '~semantic-ui-css/semantic.min.css';
@import 'flexbox';
@import 'globals';
@import 'sui-overrides';
